.App {
  text-align: center;
}



.inner {
  position: relative;
  height: auto;
}

.full-height {
  position: absolute;
  top: 0;
  left: 0;
  right: 128px;
  bottom: 0;
  height: 100%;
  
  background: blue;
}

.centered{
  display: flex;
  justify-content: center;
  align-items: center;
}

li{
  margin-top: 10px;
}

.img { 
  opacity: 0.7; 
  cursor:pointer;

} 

.img:hover { 
  opacity: 1; 
}

.figure {
  margin: 10;
  margin-left: "auto";
  margin-right: "auto";
  border-radius: 10;
  max-width: 600;
  max-height: 600;
  padding: 10;
  opacity: 0.8;

  background: white;
}

.imageStandard {
  display: inline-block;

  border-radius: 10;
  padding: 5px;
  background: white;
  border: 10px solid white;
  opacity: 0.85;
  -moz-box-shadow: 0 0 5px rgb(31, 29, 34);
  -webkit-box-shadow: 0 0 5px rgb(31, 29, 34);
  box-shadow: 0 0 5px rgb(31, 29, 34);
}

.Fixed
{
    position: fixed;
    top: 20px;
}

.highlight { color: #ffedc4 ; }


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.headerContainer{
  background: rgb(15,10,15,0.6)
  
}

.mainContainer{
  background-size: 40px 40px;
  background-image:

      linear-gradient(to right, #423b6b 1px, transparent 1px),
      linear-gradient(to bottom, #423b6b 1px, transparent 1px),
      linear-gradient(#2c2553, #2c2553);
  
}

.overlay {
  opacity: 0;
  transition: .5s ease;
  background-color: #bca5c7;
  position:"absolute;"
}

.container:hover .overlay {
  opacity: 1;
  cursor: pointer;
}

.container1{
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.container2{
  height: 100%;
  width: 100%;
  overflow: auto;
  padding-right: 20px;
}

/* Hide scrollbar for IE and Edge */
.example {
  overflow-x: hidden; 
} 

.dot {
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  position: absolute;
  
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
